import React, { useState } from 'react';
// import './Header.css';

function Header() {
  const [showlinks,setShowlinks] = useState(true);

        return (
            <header>
            <div className="top">
                <div className="hamburger" onClick={() => setShowlinks(!showlinks)}>
                    <div className="line"></div>
                    <div className="line"></div>
                    <div className="line"></div>
                </div>
            
                <div className="contai">
                    
                    <nav className="nav-link" id= {showlinks ? "hidden" : ""}>
                        <ul>
                            <li><a href="#home">Homepage</a></li>
                            <li><a href="#about">About Us</a></li>
                            <li><a href="#services">Services</a></li>
                            <li><a href="#contact">Contact</a></li>
                            <li><a className="las" href="#external">External</a></li>
                        </ul>
                    </nav>
                    
                </div>
           </div>
            <div className="round" id="home">
                <div className="hero-img"><h1 style= {{fontSize: '300%'}}>CONQUER</h1><p>Simple Bootsrtap Template</p></div>
           </div>
           </header>
        )
    
}

export default Header
